.about{
    width: calc(100% - 80px);
    margin-left: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    h2{
        width: 100%;
        text-align: center;
        color:$color-1;
        font-size: 2.5rem;
    }
   .about-content{ 
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        flex: 50%;
        max-width: 1200px;
       .block{
        flex: 50%;
        padding: 2rem 2rem;
        h4{
          font-size: 1.4rem; 
          img{
              height: 1.5rem;
              padding-right: 10px;
          } 
        }
        p{
            padding-bottom: 10px;
            font-size: 1rem;
        }
        span{
            background-color: $color-2;
            color: $color-3;
            font-size: 0.8rem;
            padding: 0.4rem;
            margin-right:0.5rem;
            border-radius: 5px;
        }
       }
   }
}
@media (min-width:600px) and (max-width: 992px){
    .about{
        width: calc(100% - 80px);
        margin-left: 80px;
        padding-bottom: 80px;
        height: auto;
        padding-top: 5vh;
    } 
}
@media screen and (max-width: 600px){
    .about{
        width: 100%;
        margin: auto;
        padding-bottom: 80px;
        height: auto;
        padding-top: 5vh;
        h2{
            font-size: 2rem;
        }
    } 
}