.home{
    width: calc(100% - 80px);
    margin-left: 80px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
   .home-content{
       > div{
          width: 50%; 
       }
       display: flex;
       align-items: center;
       .div-img{
        display: flex;
        align-items: center;
        justify-content:center;
            img{
                height: 25rem;
                border-radius: 50%;
                border:8px solid $color-1;
                box-shadow: 0px 0 5px black;
            }
       }
       .div-content{
           h1{
               font-weight: 400;
               span{
                   font-weight: 700;
               }
           }
           h2{
                font-size: 2.5rem;
                line-height: 40px;
                padding: 13px 0px;
                color: $color-1;
           }
           .title-content{
               font-weight: 100;
           }
           .action{
               display: flex;
                padding: 20px 0px 0px 0px;
            a{
                margin-right:20px ;
                background-color: $color-1;
                border:2px solid $color-1;
                color: white;
                border-radius: 20px;
                padding: 10px 15px;
                &:hover{
                    color: $color-1;
                    background-color: white;
                }
            }
            a:nth-child(2){
                color: $color-1;
                background-color: white;
                &:hover{
                    background-color: $color-1;
                    color: white;
                }
            }
           }
           
       }
       
   } 
   @media (min-width:500px) and (max-width:992px){
       height: 80vh;
    .home-content{
    flex-direction: column;
    > div{
        width: 100%;
        text-align: center;
        padding: 10px;
    }
    .div-img{
        justify-content: center;
        padding: 0;
        img{
            height: 15rem;
        }
    }
    .div-content{
        padding-top: 30px;
        .action{
            justify-content: center;
        }
        h2{
            font-size: 2rem;
            line-height: 40px;
            padding: 13px 0px;
            color: $color-1;  
        }
        h3{
            width: 90%;
            margin: auto;
        }
    }
}
}
@media screen and (max-width:500px){
    min-height: 70vh;
 .home-content{
 flex-direction: column;
 > div{
     width: 100%;
     text-align: center;
     padding: 10px;
 }
 .div-img{
     justify-content: center;
     padding: 0;
     img{
         height: 12rem;
     }
 }
 .div-content{
     padding-top: 30px;
     .action{
         justify-content: center;
         flex-direction: column;
         align-content: space-between;
         a{
             margin:10px 2rem;
         }
     }
     h1{
         font-size: 1rem;
     }
     h2{
         font-size: 1.3rem;
         line-height: 30px;
         padding: 13px 0px;
         color: $color-1;  
     }
     h3{
         font-size: 1rem;
         width: 100%;
         margin: auto;
     }
 }
}
}
}
@media screen and (max-width:600px){
    .home{
        height: auto;
        width: 87%;
        margin-left: 0px;
        margin: auto;
        padding-bottom: 80px;
        padding-top: 10px;
    }   
}