.contact{
    width: calc(100% - 80px);
    margin-left: 80px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
   .contact-content{  
       h2,h3{
           text-align: center;
       }
       h2{
           color:$color-1;
           font-size: 2.5rem;
       }
       h3{
           width: 50%;
           margin: auto;
           font-weight: 400;
           font-size: 1.3rem;
       }
       button{
           text-align: center;
            margin-right:20px ;
            background-color: $color-1;
            border:2px solid $color-1;
            color: white;
            border-radius: 20px;
            padding: 10px 15px;
            font-weight: bold;
            &:hover{
                color: $color-1;
                background-color: white;
            }
        }
        form{
            max-width: 35rem;
            margin: auto;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            margin: 0 auto;
            height: 450px;
            text-align: left;
            padding: 10px 0;
            label{
                font-weight: 700;
                text-transform: uppercase;
                font-size: 0.8rem;
                line-height: 30px;
            }
            .content,.email {
                width: 80%;
                margin: 0 auto;
                textarea {
                    width: 100%;
                    resize: none;
                }
                label {
                    display: block;
                    padding: 0;
                    margin: 0;
                }
            }
            .id {
                display: flex;
                justify-content: space-between;
                height: 80px;
                width: 80%;
                margin: 0 auto;
                .first-name, .last-name {
                    display: inline-block;
                    width: 48%;
                    label {
                        display: block;
                        padding: 0;
                        margin: 0;
                    }
                    input[name="firstname"],
                    input[name="lastname"] {
                        width: 100%;
                        height: 40px;
                    }
                }
            }
            .email {
                height: 62px;
                input[name="email"] {
                    width: 100%;
                    height: 40px;
                }
            }
            textarea{
               padding-top: 10px; 
            }
            input, textarea{
                padding-left: 10px;
                background-color: #e5e7eb;
                border-radius: 5px;
                border:1px solid #e5e7eb;
                &:active,&:focus{
                    border:1px solid $color-1;
                    background-color: white;
                    outline: none;
                }
            }
            .submit{
                display: flex;
                justify-content: center;
            }

    }
   } 
}
@media (min-width:600px) and (max-width:992px){
    .contact{
        height: 100vh;
        .contact-content{  
            h2{
                color:$color-1;
                font-size: 2rem;
            }
            h3{
                width: 70%;
                margin: auto;
                font-weight: 400;
                font-size: 1rem;
            }
        }
    }    
}
@media screen and (max-width:600px){
    .contact{
        width: 100%;
        margin: auto;
        padding-bottom: 80px;
        height: auto;
        padding-top: 5vh;
        .contact-content{ 
            h2{
                color:$color-1;
                font-size: 1.8rem;
            }
            h3{
                width: 100%;
                margin: auto;
                font-weight: 400;
                font-size: 1rem;
            }
            textarea{
                height: 7rem;
            }
        }
    }    
}